/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/

.alignleft {
    float: left;
    margin-right: 1.5em;
}

.alignright {
    float: right;
    margin-left: 1.5em;
}

.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
}


.text{
	&-left{
		text-align: left !important;
	}
	&-right{
		text-align: right !important;
	}
	&-center{
		text-align: center !important;
	}
}