@media only screen and (max-width: 767px) {
  .widget-area {
    margin-top: 3.75rem; } }

.widget {
  margin: 0 0 1.5em;
  margin-bottom: 1.875rem; }
  .widget:last-child {
    margin-bottom: 0; }
  .widget .widget-title,
  .widget .widgettitle {
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    position: relative;
    border-bottom: 1px solid #eee;
    margin-top: 0; }
    .widget .widget-title:before,
    .widget .widgettitle:before {
      width: 3.1rem;
      height: .1875rem;
      background-color: #5cca8d;
      bottom: 0;
      left: 0;
      position: absolute;
      content: ""; }
  .widget select {
    max-width: 100%;
    width: 100%;
    height: 2.5rem;
    border-color: #eee; }
  .widget .tagcloud a {
    color: #191e23;
    border: 1px solid #5cca8d;
    background-color: #ffffff;
    display: inline-block;
    text-decoration: none;
    padding: 0.625rem;
    margin: 0.1rem; }
  .widget ul,
  .widget ol {
    margin: 0;
    padding: 0;
    list-style: none; }
    .widget ul li,
    .widget ol li {
      border-bottom: 1px solid #eee;
      padding-bottom: .5rem;
      margin-bottom: .5rem; }
      .widget ul li a,
      .widget ol li a {
        text-decoration: none; }
        .widget ul li a:hover, .widget ul li a:visited, .widget ul li a:focus, .widget ul li a:active,
        .widget ol li a:hover,
        .widget ol li a:visited,
        .widget ol li a:focus,
        .widget ol li a:active {
          text-decoration: underline; }
    .widget ul ul,
    .widget ul ol,
    .widget ol ul,
    .widget ol ol {
      padding-left: 0.625rem;
      margin: 0;
      list-style: none; }
      .widget ul ul li:last-child,
      .widget ul ol li:last-child,
      .widget ol ul li:last-child,
      .widget ol ol li:last-child {
        border-bottom: 0;
        padding-bottom: 0; }
  .widget_search input {
    height: 3rem;
    width: calc(100% - 3rem);
    float: left;
    border: 1px solid #eee;
    padding-left: .8rem;
    border-radius: 0; }
    .widget_search input:focus {
      outline: none;
      border-color: #5cca8d; }
  .widget_search button {
    height: 3rem;
